.delete-btn:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.list-item {
  width: 100%;
  display: flex;
  border-bottom: solid 1px black;
  padding: 7px 20px;
  justify-content: space-between;
  align-items: center;
  
}

.movie-poster {
  width: 85px;
  border-radius: 2px;
}

.list-item > .movie-info {
  flex: 50% 1;
  margin-left: 20px;
  padding-bottom: 50px;
}

.list-item > .movie-info > .movie-title {
  font-weight: 700;
  letter-spacing: 0.03em;
}

.list-item > .movie-info > .movie-rating {
  font-weight: 400;
}

.list-item > .delete {
  flex: 25% 1;
  text-align: end;
  margin-right: 72px;
}

.list-item > .delete > .delete-icon {
  cursor: pointer;
  transition: -webkit-transform 0.45s ease;
  transition: transform 0.45s ease;
  transition: transform 0.45s ease, -webkit-transform 0.45s ease;
}

.removing {

  -webkit-animation: fadeOut .6s;

          animation: fadeOut .6s;
}
.movie-info > p {
  color: #fff;
}

.list-item > .delete > .delete-icon:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

