.link {
  display: block;
  text-align: center;
  color: #fff;
  text-decoration: none;
  padding: 10px;
}

.movie_card {
  position: relative;
  display: block;
  width: 100vw;
  height: 70vh;
  margin: 0 auto 50px auto;
  

  transition: all 0.4s;
  box-shadow: 0px 0px 120px -25px rgba(0, 0, 0, 0.5);
}

.movie_card > .info_section > .movie_header > .type {
  color: #fff;
  margin: 0 10px;
}

.movie_card > .info_section > .movie_header > .locandina {
  position: relative;
  float: left;
  margin-right: 20px;
  height: 220px;
  box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.8);
}

.movie_card > .info_section > .movie_header > .minutes {
  margin-top: 5px;
  text-shadow: rgb(0, 0, 0) 0px 0px 25px;
  color: #fff;
  margin-bottom: 30px;
}

.movie_card > .info_section > .movie_header > h4 {
  color: #fff;
  text-shadow: rgb(0, 0, 0) 0px 0px 25px;
  font-weight: 400;
  text-align: start;
  margin: 6px 0;
}

.movie_card > .info_section > .movie_header > h1 {
  color: #fff;
  font-weight: 400;
  text-shadow: rgb(0, 0, 0) 0px 0px 25px;
  text-align: start;
  margin: 0px 0;
  margin-bottom: 15px;
  
}
.movie_card > .info_section > .movie_header {
  position: relative;
  padding: 25px;
  height: 40%;
}

.movie_card > .info_section > .movie_desc {
  padding: 25px;
  height: 50%;
}

.movie_card > .info_section > .movie_desc > .text {
  color: #fff;
  text-shadow: rgb(0, 0, 0) 0px 0px 25px;
}
.movie_card > .info_section {
  position: relative;
  width: 100%;
  height: 100%;
  background-blend-mode: multiply;
  z-index: 2;
}
.external-links {
  display: flex;
  margin-top: 24px;
  align-items: center;
  overflow: visible;
  z-index: 111;
}
.imdb-logo {
  width: 90px;

  margin-right: 12px;
}

.yt-logo {
  width: 180px;
}

.movie_card > .blur_back {
  position: absolute;
  top: 0;
  z-index: 1;
  height: 100%;
  right: 0;
  background-size: cover;
}

@media screen and (min-width: 768px) {
  .movie_header {
    width: 65%;
  }

  .movie_desc {
    width: 50%;
  }

  .info_section {
    background: linear-gradient(to right, #111111 35%, transparent 100%);
  }

  .blur_back {
    width: 80%;
    background-position: -100% 10% !important;
  }
}

@media screen and (max-width: 850px) {
  .movie_card {
    width: 100%;
    margin:  auto;
    min-height: 350px;
    height: auto;
    overflow: hidden;
  }

  .blur_back {
    width: 100%;
    background-position: 50% 50% !important;
  }

  .movie_header {
    width: 100%;
    margin-top: 20px;
    padding: 0 10px !important
  }

  .movie_desc {
    width: 100%;
  }

  .movie_desc .text {
    margin-right: 40px;
  }

  .movie_desc .external-links {
    justify-content: center;
    margin: 0 0 0 -25px;
  }

  .info_section {
    background: linear-gradient(to top, #111111 50%, transparent 100%);
    display: inline-grid;
  }
}

